.App {
  text-align: center;
  background-color: cornflowerblue;
  min-height: 200vh;
}

.App-logo {
  height: 40vmin;
}

.suck {
  height: 40vmin;
  color: beige;
}

@media (prefers-reduced-motion: no-preference) {
  .ojispin {
    animation: ojisuckspin infinite 2s linear;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 10s linear;
  }
}

.App-header {
  background-color: #21aca585;
  max-height: 10vh;
  display : flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(15px + 3vmin);
  color: rgb(8, 59, 25);
}

.App-content {
  min-height: 20vh;
  font-size: calc(7px + 2vmin);
  color: rgb(34, 34, 34);
  background-color: #1794c5;
}

.siteside {
  background-color: rgb(236, 241, 243);
  min-height: 100vh;
  max-width: 50vh;
  color: rgb(71, 20, 211);
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

.bordersize {
  width:100%;
  height: 100%;
}

.void1 {
  min-height: 20vh;
}

.fontside {
  align-items: center;
  align-content: center;
  width: 13%;
}

.confine {
  box-sizing: content-box;
  display: inline-block;

}

.App-link {
  color: #d82039;
  font-size: 20px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes ojisuckspin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.react-tenor {
  background-color: #f7f7f7;
  border: 1px solid #ccc;
  max-width: 480px;
}

.react-tenor-active {
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, .2);
}

.react-tenor--search-bar {
  position: relative;
}

.react-tenor--search {
  background-color: white;
  border: 1px solid #f7f7f7;
  box-sizing: border-box;
  color: #555;
  font-family: Arial;
  font-size: 1em;
  line-height: 1.3;
  overflow: visible;
  padding: .25em .5em;
  width: 100%;
}

.react-tenor--search:focus {
  box-shadow: 0 0 2px 2px #6a89af;
  outline: none;
}

.react-tenor--autocomplete {
  box-sizing: border-box;
  color: #aaa;
  font-family: Arial;
  font-size: 1em;
  line-height: 1.3;
  left: 0;
  padding: .25em .5em;
  pointer-events: none;
  position: absolute;
  top: 1px;
}

.react-tenor--autocomplete span {
  visibility: hidden;
}

.react-tenor--spinner {
  animation: react-tenor-spin 1s linear infinite;
  height: 22px;
  position: absolute;
  right: 4px;
  top: 3px;
  width: 22px;
}

.react-tenor--spinner path {
  fill: #999;
}

.react-tenor--suggestions {
  overflow-x: auto;
  padding: .5em .5em;
  white-space: nowrap;
}

.react-tenor--suggestions button {
  background: #6a89af;
  border: 1px solid #f7f7f7;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  display: inline-block;
  font-size: 1em;
  padding: 3px 5px;
}

.react-tenor--suggestions button:focus {
  box-shadow: 0 0 2px 2px #6a89af;
  outline: none;
}

.react-tenor--suggestions button + button {
  margin-left: .5em;
}

.react-tenor--results {
  display: flex;
  flex-wrap: wrap;
  position: relative;
}

.react-tenor--result {
  background: #6a89af;
  background-image: repeating-linear-gradient(
    45deg,
    rgba(255, 255, 255, .1),
    rgba(255, 255, 255, .1) 15px,
    transparent 0,
    transparent 30px
  );
  border: 0;
  cursor: pointer;
  display: inline-block;
  flex-basis: 25%;
  height: 120px;
  opacity: 1;
  padding: 0;
  transition: opacity .3s;
  width: 120px;
}

.react-tenor--result span {
  animation: react-tenor-fade-in .2s;
  background-size: cover;
  display: block;
  height: 100%;
  width: 100%;
}

.react-tenor--result:focus {
  box-shadow: 0 0 2px 2px #6a89af;
  border: 1px solid #f7f7f7;
  outline: none;
  z-index: 1;
}

.react-tenor--result:hover {
  opacity: .5;
}

@media screen and (max-width: 480px) {
  .react-tenor--result {
    flex-basis: 33%;
  }
}

.react-tenor--page-left,
.react-tenor--page-right {
  background: #6a89af;
  border: 0;
  cursor: pointer;
  height: 1.8em;
  position: absolute;
  top: calc(50% - .9em);
  opacity: .5;
  position: absolute;
  transition: opacity .2s, left .2s, right .2s;
  width: 1.8em;
  z-index: -1;
}

.react-tenor--results:hover .react-tenor--page-left,
.react-tenor--results:hover .react-tenor--page-right {
  opacity: 1;
  z-index: 1;
}

.react-tenor--results:hover .react-tenor--page-left {
  left: -1em;
}

.react-tenor--results:hover .react-tenor--page-right {
  right: -1em;
}

.react-tenor--page-left div,
.react-tenor--page-right div {
  background: inherit;
  height: 1.6em;
  transform: rotate(45deg);
  top: .1em;
  position: absolute;
  width: 1.6em;
}

.react-tenor--page-left {
  left: -.3em;
}

.react-tenor--page-left div {
  left: -.7em;
}

.react-tenor--page-right {
  right: -.3em;
}

.react-tenor--page-right div {
  right: -.7em;
}

@keyframes react-tenor-fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes react-tenor-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}